import React, {useState} from "react";
import s from '../../Navbar/Users/Users.module.css'
import cn from 'classnames'

const Paginator = (props, {portionSize = 20}) => {
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPositionPageNum = (portionNumber - 1) * portionSize + 1
    let rightPositionPageNum = portionNumber * portionSize

    return (
        <div className={s.paginator}>
            {
                portionNumber > 1 &&
                <button className={s.butPrev} onClick={() => {
                    setPortionNumber(portionNumber - 1)
                }}>PRIV</button>
            }
            {
                pages.filter(p => p >= leftPositionPageNum && p <= rightPositionPageNum).map((p) => {
                    return <span className={cn({
                        [s.selectedPage]: props.currentPage === p
                    }, s.pageNum)}
                                 key={p} onClick={(e) => {
                        props.onPageChanged(p)
                    }}>{p}</span>
                })
            }
            {
                portionCount > portionNumber &&
                <button className={s.butNext} onClick={() => {
                    setPortionNumber(portionNumber + 1)
                }}>NEXT</button>
            }
        </div>
    )
}

export default Paginator;
