import React from "react";
import {connect} from "react-redux";
import Users from "./Users";
import {setCurrentPage, setToys, toggleFollowingProgress, requestUsers, followSuccess, unfollowSuccess, follow, unfollow
} from "../../../redax/users-reducer";
import Image from "../../common/Prelouder";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {withAuthRedirect} from "../../../HOC/withAuthRedirect";
import {compose} from "redux";
import {
    getCurrentPage,
    getFollowingInProgress, getIsAuth,
    getIsFetching,
    getPageSize,
    getTotalUsersCount,
    getUsers
} from "../../../redax/user-selectors";

class UsersContainer extends React.Component {

    componentDidMount() {
        const {currentPage, pageSize} = this.props
        this.props.getUsers(currentPage, pageSize)
    };

    onPageChanged = (pageNamber) => {
        let {pageSize} = this.props
        this.props.setCurrentPage(pageNamber);
        this.props.getUsers(pageNamber, pageSize)
    }


    render() {

        let pagesCount = Math.ceil(this.props.totalUsersCount / this.props.pageSize);
        let pages = [];
        for(let i=1; i<=pagesCount; i++){
            pages.push(i)
        };

        return <>
            { this.props.isFetching ? <Image /> : null }

            <Users totalUsersCount={this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   currentPage={this.props.currentPage}
                   onPageChanged={this.onPageChanged}
                   unfollow={this.props.unfollow}
                   follow={this.props.follow}
                   users={this.props.users}
                   setUsers={this.props.setUsers}
                   toys={this.props.toys}
                   setToys={this.props.setToys}
                   followingInProgress={this.props.followingInProgress}
                   router={ this.props.router}
                   userId={ this.props.router.params.userId}
                   isAuth={ this.props.isAuth }
            />
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        isAuth: getIsAuth(state),
        toys: state.usersPage.toys
    }
}

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}

export default compose(
    connect (mapStateToProps, { followSuccess, unfollowSuccess, setCurrentPage, toggleFollowingProgress,
        getUsers: requestUsers, unfollow, follow,setToys }),
    withRouter
)(UsersContainer)


